___CSS_LOADER_EXPORT___.push([module.id, `* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f0f0f0;
    color: #333;
    overflow: hidden;
}

.container {
    width: 100vw;
    height: 100vh;
    position: relative;
}

header {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    background-color: rgba(44, 62, 80, 0.7); /* Semi-transparent background */
    color: white;
    padding: 1rem;
    text-align: center;
    backdrop-filter: blur(5px); /* Adds a nice blur effect */
    z-index: 30;
}

header h1 {
    margin-bottom: 1rem;
}

.controls {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
    align-items: center;
    margin-bottom: 0.5rem;
}

.gesture-instructions {
    font-size: 0.9rem;
    opacity: 0.9;
    margin-top: 0.5rem;
}

.controls button {
    padding: 0.5rem 1rem;
    background-color: rgba(52, 152, 219, 0.9);
    color: white;
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s;
    backdrop-filter: blur(5px);
}

.controls button:hover {
    background-color: rgba(41, 128, 185, 0.9);
    transform: translateY(-2px);
}

.control-group {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    background-color: rgba(255, 255, 255, 0.3);
    padding: 0.3rem 0.5rem;
    border-radius: 4px;
    backdrop-filter: blur(5px);
}

.control-group label {
    font-weight: bold;
    white-space: nowrap;
}

.pen-size input[type="range"] {
    width: 80px;
}

#webcam {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: scaleX(-1); /* Mirror the webcam feed */
    z-index: 5;
}

#drawingCanvas, #landmarkCanvas {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
}

#drawingCanvas {
    z-index: 10;
    cursor: crosshair;
}

#landmarkCanvas {
    z-index: 20;
}

#instructions {
    position: absolute;
    bottom: 20px;
    left: 0;
    width: 100%;
    text-align: center;
    color: rgba(255, 255, 255, 0.9);
    background-color: rgba(0, 0, 0, 0.6);
    padding: 10px;
    font-size: 1rem;
    z-index: 25;
}

#instructions p {
    margin: 0.2rem 0;
}

#status {
    position: absolute;
    top: 20px;
    right: 20px;
    background-color: rgba(0, 0, 0, 0.7);
    color: white;
    padding: 10px 15px;
    border-radius: 20px;
    font-weight: bold;
    z-index: 35;
}

@media (max-width: 768px) {
    .controls {
        flex-direction: row;
        flex-wrap: wrap;
    }
    
    .control-group {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.2rem;
    }
    
    #instructions {
        font-size: 0.8rem;
        padding: 5px;
    }
    
    #instructions p {
        margin: 0.1rem 0;
    }
    
    .gesture-instructions {
        font-size: 0.7rem;
    }
    
    header {
        padding: 0.5rem;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;AAC1B;;AAEA;IACI,4DAA4D;IAC5D,yBAAyB;IACzB,WAAW;IACX,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,kBAAkB;AACtB;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,uCAAuC,EAAE,gCAAgC;IACzE,YAAY;IACZ,aAAa;IACb,kBAAkB;IAClB,0BAA0B,EAAE,4BAA4B;IACxD,WAAW;AACf;;AAEA;IACI,mBAAmB;AACvB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,SAAS;IACT,eAAe;IACf,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA;IACI,iBAAiB;IACjB,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,oBAAoB;IACpB,yCAAyC;IACzC,YAAY;IACZ,0CAA0C;IAC1C,kBAAkB;IAClB,eAAe;IACf,iBAAiB;IACjB,oBAAoB;IACpB,0BAA0B;AAC9B;;AAEA;IACI,yCAAyC;IACzC,2BAA2B;AAC/B;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,WAAW;IACX,0CAA0C;IAC1C,sBAAsB;IACtB,kBAAkB;IAClB,0BAA0B;AAC9B;;AAEA;IACI,iBAAiB;IACjB,mBAAmB;AACvB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,qBAAqB,EAAE,2BAA2B;IAClD,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oBAAoB;AACxB;;AAEA;IACI,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,OAAO;IACP,WAAW;IACX,kBAAkB;IAClB,+BAA+B;IAC/B,oCAAoC;IACpC,aAAa;IACb,eAAe;IACf,WAAW;AACf;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,oCAAoC;IACpC,YAAY;IACZ,kBAAkB;IAClB,mBAAmB;IACnB,iBAAiB;IACjB,WAAW;AACf;;AAEA;IACI;QACI,mBAAmB;QACnB,eAAe;IACnB;;IAEA;QACI,sBAAsB;QACtB,uBAAuB;QACvB,WAAW;IACf;;IAEA;QACI,iBAAiB;QACjB,YAAY;IAChB;;IAEA;QACI,gBAAgB;IACpB;;IAEA;QACI,iBAAiB;IACrB;;IAEA;QACI,eAAe;IACnB;AACJ","sourcesContent":["* {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n}\r\n\r\nbody {\r\n    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;\r\n    background-color: #f0f0f0;\r\n    color: #333;\r\n    overflow: hidden;\r\n}\r\n\r\n.container {\r\n    width: 100vw;\r\n    height: 100vh;\r\n    position: relative;\r\n}\r\n\r\nheader {\r\n    position: absolute;\r\n    top: 0;\r\n    left: 0;\r\n    width: 100%;\r